import api from './api';

export interface PortfolioPosition {
  assetSymbol: string;
  assetType: 'stock' | 'crypto' | 'currency';
  quantity: number;
  averagePrice: number;
  currentPrice: number;
  totalCost: number;
  currentValue: number;
  profitLoss: number;
  profitLossPercentage: number;
}

export interface PortfolioSummary {
  totalValue: number;
  totalCost: number;
  totalProfitLoss: number;
  totalProfitLossPercentage: number;
  positions: PortfolioPosition[];
  cashBalance: number;
}

export interface Transaction {
  _id: string;
  userId: string;
  assetSymbol: string;
  assetType: 'stock' | 'crypto' | 'currency';
  transactionType: 'buy' | 'sell';
  quantity: number;
  price: number;
  totalAmount: number;
  commission: number;
  timestamp: Date;
  status: 'completed' | 'pending' | 'cancelled';
  notes?: string;
  createdAt: Date;
  updatedAt: Date;
}

export interface TransactionInput {
  assetSymbol: string;
  assetType: 'stock' | 'crypto' | 'currency';
  transactionType: 'buy' | 'sell';
  quantity: number;
  price: number;
  commission?: number;
  notes?: string;
}

export interface PortfolioStats {
  portfolio: PortfolioSummary;
  transactionCount: number;
  totalTrades: number;
  winningTrades: number;
}

/**
 * API    
 */
export const portfolioApi = {
  /**
   *   
   */
  async getPortfolio(): Promise<PortfolioSummary> {
    const response = await api.get('/portfolio');
    return response.data;
  },

  /**
   *   
   */
  async getTransactions(limit?: number): Promise<Transaction[]> {
    const params = limit ? { limit } : {};
    const response = await api.get('/portfolio/transactions', { params });
    return response.data;
  },

  /**
   *  
   */
  async addTransaction(transaction: TransactionInput): Promise<Transaction> {
    const response = await api.post('/portfolio/transactions', transaction);
    return response.data;
  },

  /**
   *   
   */
  async getStats(): Promise<PortfolioStats> {
    const response = await api.get('/portfolio/stats');
    return response.data;
  },

  /**
   *  
   */
  async deleteTransaction(transactionId: string): Promise<void> {
    await api.delete(`/portfolio/transactions/${transactionId}`);
  }
};