import React, { useEffect, useState } from 'react'
import {
  Box,
  Typography,
  Card,
  CardContent,
  Chip,
  LinearProgress,
  Alert,
  Tabs,
  Tab,
} from '@mui/material'
import {
  TrendingUp,
  TrendingDown,
  Equalizer,
  Security,
  Analytics,
} from '@mui/icons-material'
import { useAppStore, appSelectors } from '../stores/appStore'

interface TabPanelProps {
  children?: React.ReactNode
  index: number
  value: number
}

const TabPanel: React.FC<TabPanelProps> = ({ children, value, index, ...other }) => {
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`analysis-tabpanel-${index}`}
      aria-labelledby={`analysis-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ py: 3 }}>{children}</Box>}
    </div>
  )
}

const Analysis: React.FC = () => {
  const { 
    loading,
    error,
    beginnerRecommendations,
    fetchBeginnerRecommendations
  } = useAppStore()

  const topRecommendations = useAppStore(appSelectors.getTopRecommendations(10))
  const [tabValue, setTabValue] = useState(0)

  useEffect(() => {
    fetchBeginnerRecommendations()
  }, [fetchBeginnerRecommendations])

  const handleTabChange = (_event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue)
  }

  const getRecommendationColor = (recommendation: string) => {
    switch (recommendation) {
      case 'BUY':
      case 'STRONG_BUY':
        return 'success'
      case 'SELL':
      case 'STRONG_SELL':
        return 'error'
      default:
        return 'warning'
    }
  }

  const getRecommendationText = (recommendation: string) => {
    switch (recommendation) {
      case 'BUY': return ''
      case 'STRONG_BUY': return ' '
      case 'SELL': return ''
      case 'STRONG_SELL': return ' '
      case 'HOLD': return ''
      default: return recommendation
    }
  }

  const getRecommendationIcon = (recommendation: string) => {
    switch (recommendation) {
      case 'BUY':
      case 'STRONG_BUY':
        return <TrendingUp />
      case 'SELL':
      case 'STRONG_SELL':
        return <TrendingDown />
      default:
        return <Equalizer />
    }
  }

  const getRiskColor = (level: string) => {
    switch (level) {
      case 'LOW': return 'success'
      case 'MEDIUM': return 'warning'
      case 'HIGH': return 'error'
      case 'VERY_HIGH': return 'error'
      default: return 'default'
    }
  }

  const getRiskText = (level: string) => {
    switch (level) {
      case 'LOW': return ''
      case 'MEDIUM': return ''
      case 'HIGH': return ''
      case 'VERY_HIGH': return ' '
      default: return ''
    }
  }

  if (loading && beginnerRecommendations.length === 0) {
    return (
      <Box>
        <Typography variant="h4" gutterBottom sx={{ mb: 4, color: (theme) => theme.palette.mode === 'light' ? 'rgba(0, 0, 0, 0.87)' : 'text.primary' }}>
           
        </Typography>
        <LinearProgress />
      </Box>
    )
  }

  return (
    <Box>
      <Typography variant="h4" gutterBottom sx={{ mb: 4, color: (theme) => theme.palette.mode === 'light' ? 'rgba(0, 0, 0, 0.87)' : 'text.primary' }}>
         
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      {/*   */}
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={tabValue} onChange={handleTabChange}>
          <Tab label="" />
          <Tab label=" " />
          <Tab label=" " />
          <Tab label="-" />
        </Tabs>
      </Box>

      {/*   */}
      <TabPanel value={tabValue} index={0}>
        <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))', gap: '24px' }}>
          <div style={{ gridColumn: '1 / -1' }}>
            <Typography variant="h6" gutterBottom>
                 
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                   ,    
            </Typography>
          </div>

          {topRecommendations.length > 0 ? (
            topRecommendations.map((rec, _index) => (
              <div key={rec.symbol}>
                <Card>
                  <CardContent>
                    <Box display="flex" alignItems="center" justifyContent="space-between" mb={2}>
                      <Typography variant="h6" component="div">
                        {rec.symbol}
                      </Typography>
                      <Chip
                        icon={getRecommendationIcon(rec.recommendation)}
                        label={getRecommendationText(rec.recommendation)}
                        color={getRecommendationColor(rec.recommendation)}
                      />
                    </Box>

                    <Box mb={2}>
                      <Typography variant="body2" color="text.secondary" gutterBottom>
                         
                      </Typography>
                      <Box display="flex" alignItems="center" gap={1}>
                        <LinearProgress 
                          variant="determinate" 
                          value={rec.confidence} 
                          sx={{ flexGrow: 1 }}
                          color={rec.confidence >= 70 ? 'success' : rec.confidence >= 50 ? 'warning' : 'error'}
                        />
                        <Typography variant="body2" fontWeight="bold">
                          {Math.round(rec.confidence)}%
                        </Typography>
                      </Box>
                    </Box>

                    <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '8px', marginBottom: '16px' }}>
                      <div>
                        <Typography variant="caption" color="text.secondary">
                          
                        </Typography>
                        <Box display="flex" alignItems="center" gap={0.5}>
                          <Security sx={{ fontSize: 16, color: getRiskColor(rec.riskLevel || 'MEDIUM') }} />
                          <Typography variant="body2">
                            {getRiskText(rec.riskLevel || 'MEDIUM')}
                          </Typography>
                        </Box>
                      </div>
                      <div>
                        <Typography variant="caption" color="text.secondary">
                           
                        </Typography>
                        <Typography variant="body2">
                          {rec.riskLevel === 'LOW' ? '2/10' : rec.riskLevel === 'MEDIUM' ? '5/10' : rec.riskLevel === 'HIGH' ? '7/10' : '9/10'}
                        </Typography>
                      </div>
                    </div>

                    <Box>
                      <Typography variant="caption" color="text.secondary" gutterBottom>
                        
                      </Typography>
                      <Typography variant="body2">
                        {rec.reasoning || '     '}
                      </Typography>
                    </Box>

                    <Box mt={2} p={1} sx={{ backgroundColor: 'background.default', borderRadius: 1 }}>
                      <Typography variant="caption" color="text.secondary" gutterBottom>
                           
                      </Typography>
                      <Typography variant="body2">
                         -   2%   
                      </Typography>
                    </Box>
                  </CardContent>
                </Card>
              </div>
            ))
          ) : (
            <div style={{ gridColumn: '1 / -1' }}>
              <Box textAlign="center" py={6}>
                <Analytics sx={{ fontSize: 64, color: 'text.secondary', mb: 2 }} />
                <Typography variant="h6" color="text.secondary" gutterBottom>
                    
                </Typography>
                <Typography variant="body2" color="text.secondary">
                        
                </Typography>
              </Box>
            </div>
          )}
        </div>
      </TabPanel>

      {/*    */}
      <TabPanel value={tabValue} index={1}>
        <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))', gap: '24px' }}>
          <div style={{ gridColumn: '1 / -1' }}>
            <Typography variant="h6" gutterBottom>
               
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                        
            </Typography>
          </div>

          <div>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Box component="ul" sx={{ pl: 2 }}>
                  <li><Typography variant="body2">RSI (Relative Strength Index)</Typography></li>
                  <li><Typography variant="body2">MACD (Moving Average Convergence Divergence)</Typography></li>
                  <li><Typography variant="body2">  (MA)</Typography></li>
                  <li><Typography variant="body2"> /</Typography></li>
                  <li><Typography variant="body2"> </Typography></li>
                </Box>
              </CardContent>
            </Card>
          </div>

          <div>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                     
                </Typography>
                <Box component="ul" sx={{ pl: 2 }}>
                  <li><Typography variant="body2"> -   (  )</Typography></li>
                  <li><Typography variant="body2">  </Typography></li>
                  <li><Typography variant="body2">   </Typography></li>
                  <li><Typography variant="body2">   </Typography></li>
                </Box>
              </CardContent>
            </Card>
          </div>
        </div>
      </TabPanel>

      {/*    */}
      <TabPanel value={tabValue} index={2}>
        <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))', gap: '24px' }}>
          <div style={{ gridColumn: '1 / -1' }}>
            <Typography variant="h6" gutterBottom>
               
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                    
            </Typography>
          </div>

          <div>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Box component="ul" sx={{ pl: 2 }}>
                  <li><Typography variant="body2">P/E (Price-to-Earnings) ratio</Typography></li>
                  <li><Typography variant="body2">P/B (Price-to-Book) ratio</Typography></li>
                  <li><Typography variant="body2"> </Typography></li>
                  <li><Typography variant="body2">   </Typography></li>
                  <li><Typography variant="body2"> </Typography></li>
                </Box>
              </CardContent>
            </Card>
          </div>

          <div>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Box component="ul" sx={{ pl: 2 }}>
                  <li><Typography variant="body2">   </Typography></li>
                  <li><Typography variant="body2">  </Typography></li>
                  <li><Typography variant="body2"> </Typography></li>
                  <li><Typography variant="body2">    </Typography></li>
                </Box>
              </CardContent>
            </Card>
          </div>
        </div>
      </TabPanel>

      {/*  - */}
      <TabPanel value={tabValue} index={3}>
        <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))', gap: '24px' }}>
          <div style={{ gridColumn: '1 / -1' }}>
            <Typography variant="h6" gutterBottom>
               
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                   
            </Typography>
          </div>

          <div>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                    -
                </Typography>
                <Box component="ul" sx={{ pl: 2 }}>
                  <li><Typography variant="body2"> 2%   </Typography></li>
                  <li><Typography variant="body2"> 5-10 </Typography></li>
                  <li><Typography variant="body2"> -</Typography></li>
                  <li><Typography variant="body2"> / 1:2</Typography></li>
                  <li><Typography variant="body2">  </Typography></li>
                </Box>
              </CardContent>
            </Card>
          </div>

          <div>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Box component="ul" sx={{ pl: 2 }}>
                  <li><Typography variant="body2"> </Typography></li>
                  <li><Typography variant="body2"> </Typography></li>
                  <li><Typography variant="body2">  </Typography></li>
                  <li><Typography variant="body2"> </Typography></li>
                  <li><Typography variant="body2"> </Typography></li>
                </Box>
              </CardContent>
            </Card>
          </div>
        </div>
      </TabPanel>
    </Box>
  )
}

export default Analysis