import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { portfolioApi, PortfolioSummary, Transaction, TransactionInput, PortfolioStats } from '../services/portfolioApi';

interface PortfolioState {
  // 
  portfolio: PortfolioSummary | null;
  transactions: Transaction[];
  stats: PortfolioStats | null;
  isLoading: boolean;
  error: string | null;

  // 
  loadPortfolio: () => Promise<void>;
  loadTransactions: (limit?: number) => Promise<void>;
  loadStats: () => Promise<void>;
  addTransaction: (transaction: TransactionInput) => Promise<void>;
  deleteTransaction: (transactionId: string) => Promise<void>;
  clearError: () => void;
  reset: () => void;
}

export const usePortfolioStore = create<PortfolioState>()(
  persist(
    (set, get) => ({
      //  
      portfolio: null,
      transactions: [],
      stats: null,
      isLoading: false,
      error: null,

      //  
      loadPortfolio: async () => {
        set({ isLoading: true, error: null });
        try {
          const portfolio = await portfolioApi.getPortfolio();
          set({ portfolio, isLoading: false });
        } catch (error: any) {
          console.error('  :', error);
          set({ 
            error: error.response?.data?.message || '   ',
            isLoading: false 
          });
        }
      },

      //   
      loadTransactions: async (limit = 50) => {
        set({ isLoading: true, error: null });
        try {
          const transactions = await portfolioApi.getTransactions(limit);
          set({ transactions, isLoading: false });
        } catch (error: any) {
          console.error('  :', error);
          set({ 
            error: error.response?.data?.message || '    ',
            isLoading: false 
          });
        }
      },

      //  
      loadStats: async () => {
        set({ isLoading: true, error: null });
        try {
          const stats = await portfolioApi.getStats();
          set({ stats, isLoading: false });
        } catch (error: any) {
          console.error('  :', error);
          set({ 
            error: error.response?.data?.message || '   ',
            isLoading: false 
          });
        }
      },

      //  
      addTransaction: async (transaction: TransactionInput) => {
        set({ isLoading: true, error: null });
        try {
          const newTransaction = await portfolioApi.addTransaction(transaction);
          const { transactions } = get();
          
          //   
          set({ 
            transactions: [newTransaction, ...transactions],
            isLoading: false 
          });

          //     
          await get().loadPortfolio();
          await get().loadStats();

        } catch (error: any) {
          console.error('  :', error);
          set({ 
            error: error.response?.data?.message || '   ',
            isLoading: false 
          });
        }
      },

      //  
      deleteTransaction: async (transactionId: string) => {
        set({ isLoading: true, error: null });
        try {
          await portfolioApi.deleteTransaction(transactionId);
          const { transactions } = get();
          
          //    
          set({ 
            transactions: transactions.filter(t => t._id !== transactionId),
            isLoading: false 
          });

          //     
          await get().loadPortfolio();
          await get().loadStats();

        } catch (error: any) {
          console.error('  :', error);
          set({ 
            error: error.response?.data?.message || '   ',
            isLoading: false 
          });
        }
      },

      //  
      clearError: () => set({ error: null }),

      //  
      reset: () => set({
        portfolio: null,
        transactions: [],
        stats: null,
        isLoading: false,
        error: null
      })
    }),
    {
      name: 'portfolio-storage',
      partialize: (state) => ({
        portfolio: state.portfolio,
        transactions: state.transactions,
        stats: state.stats
      })
    }
  )
);

//    
export const usePortfolio = () => {
  const {
    portfolio,
    transactions,
    stats,
    isLoading,
    error,
    loadPortfolio,
    loadTransactions,
    loadStats,
    addTransaction,
    deleteTransaction,
    clearError,
    reset
  } = usePortfolioStore();

  return {
    portfolio,
    transactions,
    stats,
    isLoading,
    error,
    loadPortfolio,
    loadTransactions,
    loadStats,
    addTransaction,
    deleteTransaction,
    clearError,
    reset
  };
};