import React, { useEffect, useState } from 'react';
import {
  Box,
  Typography,
  Paper,
  Card,
  CardContent,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Chip,
  LinearProgress,
  Alert,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  MenuItem,
  IconButton
} from '@mui/material';
import {
  TrendingUp,
  TrendingDown,
  AccountBalanceWallet,
  Add,
  Delete
} from '@mui/icons-material';
import { usePortfolio } from '../stores/portfolioStore';

const Portfolio: React.FC = () => {
  const {
    portfolio,
    transactions,
    isLoading,
    error,
    loadPortfolio,
    loadTransactions,
    addTransaction,
    deleteTransaction,
    clearError
  } = usePortfolio();

  const [addDialogOpen, setAddDialogOpen] = useState(false);
  const [newTransaction, setNewTransaction] = useState({
    assetSymbol: '',
    assetType: 'stock' as 'stock' | 'crypto' | 'currency',
    transactionType: 'buy' as 'buy' | 'sell',
    quantity: 0,
    price: 0,
    commission: 0,
    notes: ''
  });

  useEffect(() => {
    loadPortfolio();
    loadTransactions();
  }, [loadPortfolio, loadTransactions]);

  const handleAddTransaction = async () => {
    try {
      await addTransaction(newTransaction);
      setAddDialogOpen(false);
      setNewTransaction({
        assetSymbol: '',
        assetType: 'stock',
        transactionType: 'buy',
        quantity: 0,
        price: 0,
        commission: 0,
        notes: ''
      });
    } catch (error) {
      //    store
    }
  };

  const handleDeleteTransaction = async (transactionId: string) => {
    if (window.confirm(' ,     ?')) {
      await deleteTransaction(transactionId);
    }
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('ru-RU', {
      style: 'currency',
      currency: 'RUB'
    }).format(amount);
  };

  const formatPercentage = (value: number | undefined) => {
    if (value === undefined || value === null) return '0.00%';
    return `${value > 0 ? '+' : ''}${value.toFixed(2)}%`;
  };

  if (isLoading && !portfolio) {
    return (
      <Box sx={{ p: 3 }}>
        <LinearProgress />
        <Typography variant="h6" sx={{ mt: 2 }}>
           ...
        </Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ p: 3 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4" component="h1" sx={{ color: (theme) => theme.palette.mode === 'light' ? 'rgba(0, 0, 0, 0.87)' : 'text.primary' }}>
           
        </Typography>
        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={() => setAddDialogOpen(true)}
        >
           
        </Button>
      </Box>

      {error && (
        <Alert severity="error" onClose={clearError} sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      {/*   */}
      {portfolio && (
        <div style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))',
          gap: '24px',
          marginBottom: '32px'
        }}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <AccountBalanceWallet sx={{ mr: 1, color: 'primary.main' }} />
                <Typography variant="h6"> </Typography>
              </Box>
              <Typography variant="h4" color="primary">
                {formatCurrency(portfolio.totalValue)}
              </Typography>
            </CardContent>
          </Card>

          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <TrendingUp sx={{ mr: 1, color: 'success.main' }} />
                <Typography variant="h6">/</Typography>
              </Box>
              <Typography
                variant="h4"
                color={portfolio.totalProfitLoss >= 0 ? 'success.main' : 'error.main'}
              >
                {formatCurrency(portfolio.totalProfitLoss)}
              </Typography>
              <Typography
                variant="body2"
                color={portfolio.totalProfitLossPercentage >= 0 ? 'success.main' : 'error.main'}
              >
                {formatPercentage(portfolio.totalProfitLossPercentage)}
              </Typography>
            </CardContent>
          </Card>

          <Card>
            <CardContent>
              <Typography variant="h6" sx={{ mb: 2 }}> </Typography>
              <Typography variant="h4" color="text.secondary">
                {formatCurrency(portfolio.cashBalance)}
              </Typography>
            </CardContent>
          </Card>

          <Card>
            <CardContent>
              <Typography variant="h6" sx={{ mb: 2 }}>  </Typography>
              <Typography variant="h4" color="text.secondary">
                {portfolio.positions ? portfolio.positions.length : 0}
              </Typography>
            </CardContent>
          </Card>
        </div>
      )}

      {/*   */}
      {portfolio && portfolio.positions && portfolio.positions.length > 0 && (
        <Paper sx={{ mb: 4 }}>
          <Box sx={{ p: 2, borderBottom: 1, borderColor: 'divider' }}>
            <Typography variant="h6"> </Typography>
          </Box>
          <TableContainer>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell align="right"></TableCell>
                  <TableCell align="right"> </TableCell>
                  <TableCell align="right"> </TableCell>
                  <TableCell align="right"></TableCell>
                  <TableCell align="right">/</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {portfolio.positions.map((position) => (
                  <TableRow key={position.assetSymbol}>
                    <TableCell>
                      <Typography variant="subtitle1">
                        {position.assetSymbol}
                      </Typography>
                    </TableCell>
                    <TableCell>
                      <Chip 
                        label={position.assetType === 'stock' ? '' : ''} 
                        size="small" 
                        color={position.assetType === 'stock' ? 'primary' : 'secondary'}
                      />
                    </TableCell>
                    <TableCell align="right">
                      {position.quantity.toLocaleString('ru-RU')}
                    </TableCell>
                    <TableCell align="right">
                      {formatCurrency(position.averagePrice)}
                    </TableCell>
                    <TableCell align="right">
                      {formatCurrency(position.currentPrice)}
                    </TableCell>
                    <TableCell align="right">
                      {formatCurrency(position.currentValue)}
                    </TableCell>
                    <TableCell align="right">
                      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-end' }}>
                        {position.profitLoss >= 0 ? (
                          <TrendingUp sx={{ color: 'success.main', mr: 0.5 }} />
                        ) : (
                          <TrendingDown sx={{ color: 'error.main', mr: 0.5 }} />
                        )}
                        <Typography 
                          variant="body2" 
                          color={position.profitLoss >= 0 ? 'success.main' : 'error.main'}
                        >
                          {formatCurrency(position.profitLoss)}
                          <br />
                          {formatPercentage(position.profitLossPercentage)}
                        </Typography>
                      </Box>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>
      )}

      {/*   */}
      {transactions && transactions.length > 0 && (
        <Paper>
          <Box sx={{ p: 2, borderBottom: 1, borderColor: 'divider' }}>
            <Typography variant="h6"> </Typography>
          </Box>
          <TableContainer>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell align="right"></TableCell>
                  <TableCell align="right"></TableCell>
                  <TableCell align="right"></TableCell>
                  <TableCell></TableCell>
                  <TableCell align="center"></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {transactions.map((transaction) => (
                  <TableRow key={transaction._id}>
                    <TableCell>
                      {new Date(transaction.timestamp).toLocaleDateString('ru-RU')}
                    </TableCell>
                    <TableCell>{transaction.assetSymbol}</TableCell>
                    <TableCell>
                      <Chip
                        label={transaction.transactionType === 'buy' ? '' : ''}
                        size="small"
                        color={transaction.transactionType === 'buy' ? 'success' : 'error'}
                      />
                    </TableCell>
                    <TableCell align="right">
                      {transaction.quantity.toLocaleString('ru-RU')}
                    </TableCell>
                    <TableCell align="right">
                      {formatCurrency(transaction.price)}
                    </TableCell>
                    <TableCell align="right">
                      {formatCurrency(transaction.totalAmount)}
                    </TableCell>
                    <TableCell>{transaction.notes || '-'}</TableCell>
                    <TableCell align="center">
                      <IconButton
                        size="small"
                        color="error"
                        onClick={() => handleDeleteTransaction(transaction._id)}
                      >
                        <Delete />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>
      )}

      {/*    */}
      <Dialog open={addDialogOpen} onClose={() => setAddDialogOpen(false)} maxWidth="sm" fullWidth>
        <DialogTitle> </DialogTitle>
        <DialogContent>
          <div style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))',
            gap: '16px',
            marginTop: '8px'
          }}>
            <div style={{ gridColumn: '1 / -1' }}>
              <TextField
                fullWidth
                label=" "
                value={newTransaction.assetSymbol}
                onChange={(e) => setNewTransaction({
                  ...newTransaction,
                  assetSymbol: e.target.value.toUpperCase()
                })}
                placeholder=": SBER, BTCUSDT"
              />
            </div>
            <div>
              <TextField
                fullWidth
                select
                label=" "
                value={newTransaction.assetType}
                onChange={(e) => setNewTransaction({
                  ...newTransaction,
                  assetType: e.target.value as 'stock' | 'crypto' | 'currency'
                })}
              >
                <MenuItem value="stock"></MenuItem>
                <MenuItem value="crypto"></MenuItem>
              </TextField>
            </div>
            <div>
              <TextField
                fullWidth
                select
                label=" "
                value={newTransaction.transactionType}
                onChange={(e) => setNewTransaction({
                  ...newTransaction,
                  transactionType: e.target.value as 'buy' | 'sell'
                })}
              >
                <MenuItem value="buy"></MenuItem>
                <MenuItem value="sell"></MenuItem>
              </TextField>
            </div>
            <div>
              <TextField
                fullWidth
                type="number"
                label=""
                value={newTransaction.quantity}
                onChange={(e) => setNewTransaction({
                  ...newTransaction,
                  quantity: parseFloat(e.target.value) || 0
                })}
              />
            </div>
            <div>
              <TextField
                fullWidth
                type="number"
                label="  "
                value={newTransaction.price}
                onChange={(e) => setNewTransaction({
                  ...newTransaction,
                  price: parseFloat(e.target.value) || 0
                })}
              />
            </div>
            <div style={{ gridColumn: '1 / -1' }}>
              <TextField
                fullWidth
                type="number"
                label=""
                value={newTransaction.commission}
                onChange={(e) => setNewTransaction({
                  ...newTransaction,
                  commission: parseFloat(e.target.value) || 0
                })}
              />
            </div>
            <div style={{ gridColumn: '1 / -1' }}>
              <TextField
                fullWidth
                label=""
                value={newTransaction.notes}
                onChange={(e) => setNewTransaction({
                  ...newTransaction,
                  notes: e.target.value
                })}
                multiline
                rows={2}
              />
            </div>
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setAddDialogOpen(false)}></Button>
          <Button 
            onClick={handleAddTransaction}
            variant="contained"
            disabled={!newTransaction.assetSymbol || newTransaction.quantity <= 0 || newTransaction.price <= 0}
          >
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default Portfolio;